*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 14px;
  line-height: 1.5;
  text-align: center;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  min-height: 100vh;
  height: 100vh;
  display: flex;
  flex-wrap: wrap;
  /* display: grid;
  place-items: center;
  grid-template-rows: 30% 10% 1fr; */
  background: hsl(0deg, 0%, 95%);
  font-family: "Balsamiq Sans", cursive;
  color: hsl(0deg, 0%, 30%);
}

body > .hero {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  position: relative;
  z-index: 1;
}
body > .hero ::before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-image: url(../../images/hero-background.jpg);
  background-size: cover;
  background-position: center;
  opacity: 0.4;
}

.hero > h1 {
  font-size: 1.5rem;
  line-height: 2;
}

.hero > h1 > span {
  display: block;
  font-size: 15vw;
}

body > .banner {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 15%;
  width: 100%;
}

.portfolio {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  height: 100%;
  width: 100%;
}

.portfolio-animated {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100%;
  width: 80%;
}
.portfolio-animated .portfolio-title h1 {
  font-size: 3em;
  font-style: italic;
}
.portfolio-animated .portfolio-image {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 25%;
  width: 100%;
}
.portfolio-animated .portfolio-image img {
  margin-top: 15%;
  height: 100%;
  width: auto;
  border: 5px double hsl(0deg, 0%, 65%);
}
.portfolio-animated .portfolio-content {
  display: flex;
  height: 75%;
  width: 100%;
  justify-content: center;
  align-items: center;
  text-align: justify;
}
.portfolio-animated .portfolio-content .about_me {
  display: flex;
  height: 80%;
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  text-align: justify;
  padding: 2.5%;
}

.experience {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 150vh;
  width: 100%;
}
.experience .experience-title h1 {
  font-size: 3em;
  font-style: italic;
}
.experience .experience-animated {
  display: grid;
  place-items: center;
  grid-template-rows: 30% 30% 30%;
  padding: 0 2.5%;
  height: 100%;
  width: 80%;
  box-shadow: 0 0 10px 5px hsl(0deg, 0%, 65%);
  margin-bottom: 5%;
}
.experience .experience-animated .experience-card {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  height: 100%;
  width: 100%;
}
.experience .experience-animated .experience-card .experience-workplace h1 {
  margin: 0.5em 0;
  border-bottom: 2px solid hsl(0deg, 0%, 15%);
}
.experience .experience-animated .experience-card .experience-time {
  margin-top: 5%;
  padding: 3%;
  border: 1px solid hsl(0deg, 0%, 15%);
  border-radius: 50%;
  background-color: hsl(0deg, 0%, 65%);
}

.skills {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80%;
  width: 100%;
  background-color: lightsteelblue;
}

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 20%;
  width: 100%;
  background-color: aqua;
}